export default {
	data() {
		return {
			modalName: null,
			headerPosition: "fixed",
			headerTop: "0px",
			orderInfo:null,
			typeText: {
				WP: '等待付款',
				WR: '等待用户评价',
				YR: '交易已完成',
				WDD: '退款处理中',
				DD: '订单已取消',
				SDD: '已退款',
				FDD: '拒绝退款'
			},
			orderType: ['全部', '待付款', '待评价', '已完成', '售后'],
			list: [],
			tabbarIndex: 0,
			status: null //订单状态: ALL:全部 WP:待付款 WR:待评价 DD:已取消 AS:售后
		};
	},
	onLoad() {
		let mid = this.getMerchantInfo().id;
		if(mid){
			this.loadOrder(0);
		}else{
			uni.showToast({
				title:"您还未认证商家",
				icon:"none"
			})
			setTimeout(()=>{
				uni.switchTab({
					url:"../tabbar/data"
				})
			},1000)
		}
	},
	onPageScroll(e) {
		return;
		this.headerPosition = e.scrollTop >= 0 ? "fixed" : "absolute";
	},
	methods: {
		loadOrder(tbIndex) {
			switch (tbIndex) {
				case 0:
					this.status = "ALL";
					break;
				case 1:
					this.status = 'WP';
					break;
				case 2:
					this.status = 'WR';
					break;
				case 3:
					this.status = 'YR';
					break;
				case 4:
					this.status = 'DD';
					break;
			}
			setTimeout(() => {
				uni.request({
					url: this.prefix + "/orderPage",
					data: {
						status: this.status,
						mid: this.getMerchantInfo().id,
						size: 50
					},
					success: (res) => {
						this.list = res.data.data;
						this.tabbarIndex = tbIndex;
					},
				})
			}, 10)

		},
		//跳转
		toRoom(e) {
			uni.navigateTo({
				url: "../room/room?id=" + e
			})
		},
		//评价跳转
		toComment(oId, rId) {
			uni.navigateTo({
				url: "./comment?oId=" + oId + "&rId=" + rId
			})
		},
		//取消订单
		cancelOrder(row) {
			uni.showModal({
				title: '',
				content: '确定删除此订单？',
				success: (res) => {
					if (res.confirm) {
						uni.request({
							url: this.prefix + "/doOpeOrder",
							data: {
								status: "D",
								id: row.id
							},
							success: (res) => {
								this.list.splice(this.list.indexOf(row.id), 1);
							}
						})
					}
				}
			});
		},
		//退款
		agree(row) {
			uni.showModal({
				title: '',
				content: '是否同意退款？',
				success: (res) => {
					if (res.confirm) {
						uni.request({
							url: this.prefix + "/doOpeOrder",
							data: {
								status: "SDD",
								id: row.id
							},
							success: (res) => {
								uni.showToast({
									title: "退款成功",
									icon: "none"
								})
								this.loadOrder(4);
							}
						})
					}
				}
			});
		},
		//拒绝退款
		refuse(row) {
			uni.showModal({
				title: '',
				content: '是否拒绝退款？',
				success: (res) => {
					if (res.confirm) {
						uni.request({
							url: this.prefix + "/doOpeOrder",
							data: {
								status: "FDD",
								id: row.id
							},
							success: (res) => {
								uni.showToast({
									title: "已拒绝",
									icon: "none"
								})
								this.loadOrder(4);
							}
						})
					}
				}
			});
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
			this.orderInfo = this.list[e.currentTarget.dataset.index]
		},
		hideModal(e) {
			this.modalName = null
		}
	}
}
